.nav-container {
    background-color: rgb(46, 46, 46);
    display: flex;
    justify-content: flex-end;
    
    ul {
        display: flex;
        li {
            padding: 1rem;
            a {
                color: white;
                &.active {
                    color: rgb(170, 170, 211);
                }
            }
        }
    }
}