.edit-page {
    text-align: center;
    margin: 20px;
    color: rgb(46, 46, 46);

    input {
        margin: 10px;
    }

    a {
        font-family: inherit;
        cursor: pointer;
        background-color: rgb(170, 170, 211);
        color: rgb(46, 46, 46);
        border: 1px solid rgb(46, 46, 46);
        border-radius: 5000px;
        padding: 5px 15px;
        transition: .3s;

        &:hover {
            color: rgb(170, 170, 211);
            background-color: rgb(46, 46, 46);
        }
    }

    button {
        margin: 10px;
    }

    .nav-btns {
        button {
            background-color: rgb(255, 92, 92);

            &:hover {
                background-color: rgb(110, 0, 0);
            }
        }
    }
}