* {
    box-sizing: border-box;
}

html, body, div, ul, li, h1, h2, h3, h4, p {
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: cursive;
}

input {
    font-family: inherit;
}

button {
    font-family: inherit;
    font-size: 1rem;
    cursor: pointer;
    background-color: rgb(170, 170, 211);
    color: rgb(46, 46, 46);
    border: 1px solid rgb(46, 46, 46);
    border-radius: 5000px;
    padding: 5px 15px;
    transition: .3s;

    &:hover {
        color: rgb(170, 170, 211);
        background-color: rgb(46, 46, 46);
    }
}