.details-page {
    text-align: center;

    input {
        margin: 10px;
    }

    .btns {
        margin-top: 20px;
        margin-bottom: 40px;
        a {
            margin: 10px;
            font-family: inherit;
            cursor: pointer;
            background-color: rgb(170, 170, 211);
            color: rgb(46, 46, 46);
            border: 1px solid rgb(46, 46, 46);
            border-radius: 5000px;
            padding: 5px 15px;
            transition: .3s;
    
            &:hover {
                color: rgb(170, 170, 211);
                background-color: rgb(46, 46, 46);
            }
        }
    }
}