.contact-page {
    padding: 20px;
    position: relative;
    .add-btn {
        position: absolute;
        top: 50px;
        right: 50px;
        padding: 10px 30px;
        font-size: 3rem;
        border-radius: 50%;
        background-color: rgb(170, 170, 211);
        color: rgb(46, 46, 46);
        transition: .3s;

        color: white;
        &.active {
            color: white;
        }

        &:hover {
            color: rgb(170, 170, 211);
            background-color: rgb(46, 46, 46);
        }
    }
}