li {
    list-style: none;
}

.contact-preview {
    cursor: pointer;
    width: 50%;
    background-color: rgb(170, 170, 211);
    border: 2px solid rgba(0, 0, 0, 0);
    border-radius: 10px;
    margin-top: 10px;
    display: flex;
    align-items: center;
    transition: .3s;

    img {
        height: 100px;
        width: 100px;
        margin-right: 20px;
    }

    span {
        font-size: 1.5rem;
        color: rgb(46, 46, 46);
    }

    &:hover {
        border: 2px solid rgb(0, 0, 0);
    }
}